import {
  React,
  Field,
  reduxForm,
  connect,
  ReanderSelect,
  Component,
} from "../../../components/helpers/library";
import { GetDataGroup, getDataJenis } from "./Module";
import ValidasiFilterTambahBarang from "../../../Validasi/Barang/TambahBarang/ValidasiFilterTambahBarang";
import { getData2 } from "../../../config/axios";

const maptostate = (state) => {
  return {
    DataGroup: state.datamaster.GetDataGroup,
    DataJenis: state.datamaster.getDataJenis,
    // DataBaki: state.datamaster.getDataBaki,
  };
};
class FIlterPencarianBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      kondisi_barang: "kosong",
      databaki: [],
    };
  }
  componentDidMount() {
    this.props.dispatch(GetDataGroup());
    this.props.dispatch(getDataJenis());
    // this.props.dispatch(getDataBaki());

    getData2("baki/get/tambah-barang").then((res) => {
      this.setState({
        databaki: res.data,
      });
    });
  }
  render() {
    return (
      <div>
        <form
          onSubmit={this.props.handleSubmit}
          autoComplete="off"
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
        >
          <div className="row">
            <div className="col-lg-2">
              <Field
                label="Kode Group"
                name="kode_kategori"
                component={ReanderSelect}
                onChange={(e) => this.setState({ kode_group: e })}
                options={this.props.DataGroup.filter(
                  (list) => list.kode_group !== undefined
                ).map((list) => {
                  let data = {
                    value: list.kode_group,
                    name: list.kode_group,
                  };
                  return data;
                })}
                placeholder="Silahkan Pilih Kode Group"
              />
            </div>
            <div className="col-lg-2">
              <Field
                name="kode_jenis"
                component={ReanderSelect}
                options={this.props.DataJenis.filter(
                  (list) => list.kode_group === this.state.kode_group
                ).map((list) => {
                  let data = {
                    value: list.kode_dept,
                    name: list.kode_dept,
                  };
                  return data;
                })}
                label="Kode Jenis"
                placeholder="Silahkan Pilih Kode Jenis"
              />
            </div>
            <div className="col-lg-2">
              <Field
                name="kode_lokasi_toko"
                component={ReanderSelect}
                options={this.state.databaki
                  .filter((list) => list.kode_baki !== undefined)
                  .map((list) => {
                    let data = {
                      value: list.kode_baki + "~" + list.kode_gudang,
                      name: list.kode_baki,
                    };
                    return data;
                  })}
                label="Kode Baki"
                placeholder="Silahkan Pilih Kode Lokasi"
              />
            </div>
            <div className="col-lg-1">
              <label>&nbsp;</label>
              <br />
              <button type="submit" className="btn btn-block btn-primary ">
                {" "}
                <i className="fa fa-plus"></i>{" "}
              </button>
            </div>
            {/* <div className="col-lg-3 text-left">
              <label>&nbsp;</label>
              <br />
              <button
                className="btn btn-primary"
                onClick={this.props.barangPesanan}
                type="button"
              >
                Barang Pesanan
              </button>
            </div> */}
            <div className="col-lg-5 text-right">
              <label>&nbsp;</label>
              <br />
              <button className="btn btn-primary">Cetak Barcode</button>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

FIlterPencarianBarang = reduxForm({
  form: "FIlterPencarianBarang",
  enableReinitialize: true,
  validate: ValidasiFilterTambahBarang,
})(FIlterPencarianBarang);
export default connect(maptostate)(FIlterPencarianBarang);
