import {
  React,
  Component,
  Camera,
  HiidenFiled,
  ReanderField,
  createNumberMask,
  Field,
  reduxForm,
  connect,
  showModal,
  convertBase64,
  ToastNotification,
  getData,
  Gramasi,
  imgfound,
  hideModal,
} from "../../../../components/helpers/library";
import { getData2 } from "../../../../config/axios";
import ValidasiFilterTambahBarang from "../../../../Validasi/Barang/TambahBarang/ValidasiFilterTambahBarang";
import { setCamera } from "../Module";

const maptostate = (state) => {
  let kode = state.databarang.showEditBarang.kode_lokasi_toko.split("~");
  return {
    // DataBandrol: state.datamaster.getBandrol,
    initialValues: {
      kode_kategori: state.databarang.showEditBarang.kode_kategori,
      kode_jenis: state.databarang.showEditBarang.kode_jenis,
      kode_lokasi_toko: kode[0],
      kode_gudang: kode[1],
      kode_intern: state.databarang.showEditBarang.kode_intern,
      kode_barcode: state.databarang.showEditBarang.kode_barcode,
      nama_barang: state.databarang.showEditBarang.nama_barang,
      // berat: state.databarang.showEditBarang.berat,
      kadar: state.databarang.showEditBarang.kadar,
      kadar_cetak: state.databarang.showEditBarang.kadar_cetak,
      kadar_modal: state.databarang.showEditBarang.kadar_modal,
      nama_atribut: state.databarang.showEditBarang.nama_atribut,
      berat_atribut: state.databarang.showEditBarang.berat_atribut,
      harga_atribut: state.databarang.showEditBarang.harga_atribut,
      tag_id: state.databarang.showEditBarang.tag_id || "-",
      // berat_asli: state.databarang.showEditBarang.berat_asli,
      berat_bandrol: state.databarang.showEditBarang.berat_bandrol,
    },
    foto: state.databarang.showEditBarang.foto,
    setCamera: state.databarang.setCamera,
    hideModal: state.datamaster.modalDialog,
  };
};
const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "id-ID",
});

class FormTambahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: true,
      ModalWebCam: false,
      isLoadingBaki: false,
      isLoadingJenis: false,
      kategori_harga: true,
      tgl_awal: new Date(),
      tgl_akhir: new Date(),
      databaki: [],
      DataGroup: [],
      DataJenis: [],
      loadimage: imgfound,
    };
  }
  tgl_awalChange = (date) => {
    this.setState({
      tgl_awal: date,
    });
  };
  tgl_akhirChange = (date) => {
    this.setState({
      tgl_akhir: date,
    });
  };
  showModalWebcam() {
    this.props.dispatch(setCamera(false));
    this.props.dispatch(showModal());
    this.setState({ ModalWebCam: true });
  }
  handleTakePhoto(url) {
    // console.log(url);
    document.getElementById("kode_intern").focus();
    this.props.dispatch(setCamera(url));
    this.props.change("foto", url);
  }

  componentDidMount() {
    // this.props.dispatch(getBandrol());
    setTimeout(() => {
      getData("parabandrol/get/all").then((res) => {
        this.props.change("berat_bandrol", res.data[0].berat_bandrol);
      });
      document.getElementById("kode_intern").focus();
    }, 100);
    getData2("para-system/key/kategori_harga").then((res) => {
      console.log(res.data.value);
      if (res.data.value === "harga_100%") {
        this.setState({
          kategori_harga: true,
        });
      } else {
        this.setState({
          kategori_harga: false,
        });
      }

      this.props.change("kadar", this.state.kategori_harga ? 0 : 100);
    });
  }

  getValue = async (event) => {
    document.getElementById("kode_intern").focus();
    const file = event.target.files[0];
    const base64 = await convertBase64(file);
    this.props.dispatch(setCamera(base64));
    this.props.change("foto", base64);
  };

  getJenis(params) {
    this.setState({ isLoadingJenis: true });
    getData("jenis/get/by-kode-kategori/" + params)
      .then((res) => {
        this.setState({
          DataJenis: res.data,
        });
      })
      .then((res) => {
        this.setState({
          isLoadingJenis: false,
        });
      })
      .catch((err) => {
        ToastNotification("info", err.data);
        console.log(err);
      });
  }
  getBaki(params) {
    this.setState({ isLoadingBaki: true });
    getData("baki/get/by-kode-baki/" + params)
      .then((res) => {
        this.setState({
          databaki: res.data === "" ? [] : [res.data],
        });
      })
      .then((res) => {
        this.setState({
          isLoadingBaki: false,
        });
      })
      .catch((err) => {
        ToastNotification("info", err.data);
        console.log(err);
      });
  }
  loadcamera() {
    return (
      <Camera
        onTakePhoto={(dataUri) => {
          this.handleTakePhoto(dataUri);
        }}
      />
    );
  }
  render() {
    return (
      <>
        <form onSubmit={this.props.handleSubmit} autoComplete="off">
          <div className="row">
            <div className="col-lg-12 text-center">
              {this.state.ModalWebCam === true ? (
                this.props.setCamera === false ? (
                  <div className="container">{this.loadcamera()}</div>
                ) : (
                  <>
                    <img
                      src={
                        this.props.foto === undefined
                          ? this.props.setCamera === false
                            ? imgfound
                            : this.props.setCamera
                          : this.props.setCamera
                          ? this.props.setCamera
                          : this.props.foto
                      }
                      alt="img"
                      height="245px"
                      width="355px"
                    />
                  </>
                )
              ) : (
                <>
                  <img
                    src={
                      this.props.foto === undefined
                        ? this.props.setCamera === false
                          ? imgfound
                          : this.props.setCamera
                        : this.props.setCamera
                        ? this.props.setCamera
                        : this.props.foto
                    }
                    alt="img"
                    height="245px"
                    width="355px"
                  />
                </>
              )}
            </div>
          </div>
          <br />
          <div className="row">
            <div className="col-2"> </div>
            <div className="col-5">
              <div className="input-group">
                <div className="custom-file">
                  <input
                    tabIndex="1"
                    type="file"
                    name="photo"
                    autoComplete="off"
                    className="custom-file-input"
                    id="exampleInputFile"
                    onChange={(e) => this.getValue(e)}
                  />
                  <label
                    className="custom-file-label"
                    htmlFor="exampleInputFile"
                  >
                    Pilih gambar
                  </label>
                </div>
              </div>
            </div>
            <div className="col-3">
              <button
                tabIndex="2"
                className="btn btn-block btn-primary"
                type="button"
                onClick={() => this.showModalWebcam()}
              >
                {" "}
                WebCam{" "}
              </button>
            </div>
            <hr />
          </div>
          <hr />
          <div className="row">
            <div className="col-lg-4">
              <Field
                readOnly={true}
                name="kode_kategori"
                component={ReanderField}
                label="Kategori"
                placeholder="Masukan Kategori"
              />
            </div>
            <div className="col-lg-4">
              <Field
                readOnly={true}
                name="kode_jenis"
                component={ReanderField}
                label="Jenis"
                placeholder="Masukan Jenis"
              />
            </div>
            <div className="col-lg-4">
              <Field
                readOnly={true}
                name="kode_lokasi_toko"
                component={ReanderField}
                label="Kode Lokasi Toko"
                placeholder="Masukan Lokasi"
              />
            </div>
          </div>
          <hr />
          <div className="row">
            <div className="col-6">
              <Field
                readOnly={true}
                name="kode_gudang"
                type="hidden"
                component={HiidenFiled}
              />
              <Field
                readOnly={true}
                name="foto"
                type="hidden"
                component={HiidenFiled}
              />
              <Field
                readOnly={true}
                name="kode_barcode"
                type="hidden"
                component={HiidenFiled}
              />

              <Field
                id="kode_intern"
                tabIndex="3"
                name="kode_intern"
                component={ReanderField}
                label="Kode Intern"
                placeholder="Masukan Kode Intern"
              />
            </div>
            <div className="col-6"></div>
            <div className="col-12">
              <Field
                tabIndex="4"
                name="nama_barang"
                component={ReanderField}
                label="Nama Barang"
                placeholder="Masukan Nama Barang"
              />
            </div>
            <div className="col-lg-6">
              <Field
                tabIndex="5"
                type="text"
                name="berat"
                component={ReanderField}
                label="Berat"
                normalize={Gramasi}
                placeholder="Masukan Berat"
              />
            </div>
            <div className="col-lg-6">
              <Field
                tabIndex="6"
                type="text"
                name="berat_asli"
                component={ReanderField}
                label="Berat Asli"
                normalize={Gramasi}
                placeholder="Masukan Berat Asli"
              />
            </div>
            <div
              className={
                this.state.kategori_harga ? "col-lg-6" : "col-lg-6 d-none"
              }
            >
              <Field
                tabIndex="7"
                type="text"
                name="kadar"
                component={ReanderField}
                label="Kadar"
                normalize={Gramasi}
                placeholder="Masukan Kadar"
              />
            </div>
            <div className="col-lg-3">
              <Field
                tabIndex="8"
                type="text"
                name="kadar_cetak"
                component={ReanderField}
                label="Kadar Cetak"
                normalize={Gramasi}
                placeholder="Masukan Kadar Cetak"
              />
            </div>
            <div className="col-lg-3">
              <Field
                tabIndex="9"
                type="text"
                name="kadar_modal"
                normalize={Gramasi}
                component={ReanderField}
                label="Kadar Modal"
                placeholder="Masukan Kadar Modal"
              />
            </div>
          </div>
          <hr />
          <div className="row">
            <div className="col-12">
              <Field
                id="nama_atribut"
                tabIndex="10"
                type="text"
                name="nama_atribut"
                component={ReanderField}
                label="Nama Atribut"
                placeholder="Masukan Nama Atribut"
              />
            </div>
            <div className="col-lg-6">
              <Field
                tabIndex="11"
                type="text"
                name="berat_atribut"
                component={ReanderField}
                label="Berat Atribut"
                normalize={Gramasi}
                placeholder="Masukan Berat Atribut"
              />
            </div>
            <div className="col-lg-6">
              <Field
                tabIndex="12"
                name="harga_atribut"
                component={ReanderField}
                label="Harga Atribut"
                {...currencyMask}
                placeholder="Masukan Harga Atribut"
              />
            </div>
          </div>
          <hr />
          <div className="row">
            <div className="col-lg-6 d-none">
              <Field
                tabIndex="13"
                name="tag_id"
                component={HiidenFiled}
                type="hidden"
                label="Tag Id"
                placeholder="Masukan Tag Id"
              />
            </div>
            <div className="col-lg-6 d-none">
              <Field
                tabIndex="14"
                type="text"
                name="berat_bandrol"
                component={ReanderField}
                label="Berat Bandrol"
                normalize={Gramasi}
                placeholder="Masukan Berat Bandrol"
              />
            </div>
          </div>
          <hr />
          <div className="row">
            <div className="col-6"></div>
            <div className="col-lg-6 text-right">
              {/* <div className="form-group">
                <label> &nbsp; </label> */}
              <button
                // tabIndex="14"
                onClick={() => this.props.dispatch(hideModal())}
                type="button"
                className="btn btn-secondary"
              >
                Batal
              </button>
              &nbsp;
              <button
                tabIndex="15"
                disabled={this.props.isLoading}
                type="submit"
                className="btn btn-success"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Menyimpan
                  </>
                ) : (
                  "Simpan"
                )}
              </button>
            </div>
          </div>
          {/* </div> */}
        </form>
      </>
    );
  }
}

FormTambahBarang = reduxForm({
  form: "FormTambahBarang",
  enableReinitialize: true,
  validate: ValidasiFilterTambahBarang,
})(FormTambahBarang);
export default connect(maptostate)(FormTambahBarang);
